import PocketBase from 'pocketbase'
export const pb = new PocketBase('http://localhost:8090')

export interface Mode {
    name: string
    color: string
    id: string
}

export interface Project {
    name: string
    id: string
}

export interface Task {
    id: string
    name: string
    estimated: number
    actual: number
    expand?: {
        mode: Mode
        project: Project
    }
}
