<script lang="ts">
    // imports
    import type { Mode } from './db'
    import PocketBase from 'pocketbase'
    const pb = new PocketBase('http://localhost:8090')

    // props
    export let color = 'black'
    export let value = ''

    // states
    let promise: Promise<Mode[]>

    promise = pb.collection('tc_modes').getFullList()
</script>

{#await promise}
    <p>Loading...</p>
{:then modes}
    <select name="mode" id="mode" style:color {value} on:change>
        {#each modes as mode (mode.id)}
            <option value={mode.id} style:color={mode.color}>{mode.name}</option
            >
        {/each}
    </select>
{:catch error}
    <p>{error}</p>
{/await}
