<script lang="ts">
  import type { Task, Mode, Project } from './db'
  import PocketBase from 'pocketbase'
  const pb = new PocketBase('http://localhost:8090')

  let promise: Promise<[Task[], Mode[], Project[]]>

  function fetchData() {
    const tasksPromise = pb
      .collection('tc_tasks')
      .getFullList<Task>({ expand: 'mode,project' })
    const modesPromise = pb.collection('tc_modes').getFullList<Mode>()
    const projectsPromise = pb.collection('tc_projects').getFullList<Project>()
    return Promise.all([tasksPromise, modesPromise, projectsPromise])
  }
  promise = fetchData()

  async function handleChange(e: Event) {
    if (
      !(e.currentTarget instanceof HTMLInputElement) &&
      !(e.currentTarget instanceof HTMLSelectElement)
    )
      return
    const el = e.currentTarget
    const id = el.dataset.id
    const field = el.dataset.field
    const value = el.value
    const data = { [field]: value }
    console.log(data)
    await pb.collection('tc_tasks').update(id, data)
    promise = fetchData()
  }
</script>

<div class="container">
  {#await promise}
    <p>Loading...</p>
  {:then [tasks, modes, projects]}
    {#each tasks as task (task.id)}
      <div class="mb-3 h-8 border-b-2" style:color={task.expand.mode.color}>
        <input
          type="text"
          value={task.name}
          class=""
          data-id={task.id}
          data-field="name"
          on:change={handleChange}
        />
        <select
          name="mode"
          id="mode"
          value={task.expand.mode.id}
          data-id={task.id}
          data-field="mode"
          on:change={handleChange}
        >
          {#each modes as mode (mode.id)}
            <option value={mode.id} style:color={mode.color}>{mode.name}</option
            >
          {/each}
        </select>
        <select
          name="project"
          id="project"
          value={task.expand.project.id}
          data-id={task.id}
          data-field="project"
          on:change={handleChange}
        >
          {#each projects as project (project.id)}
            <option value={project.id}>{project.name}</option>
          {/each}
        </select>
      </div>
    {/each}
  {:catch error}
    <p class="bg-red-500">error:{error.message}</p>
  {/await}
</div>
