<script lang="ts">
    import { pb } from './db'
    import type { Mode } from './db'
    import Select from './Select.svelte'

    export let value: string = ''

    let promise: Promise<Mode[]>

    promise = pb.collection('tc_modes').getFullList<Mode>()
</script>

{#await promise}
    <p>Loading...</p>
{:then modes}
    <Select options={modes} {value} on:change />
{:catch error}
    <p>{error}</p>
{/await}
