<script lang="ts">
    export let value: string
    export let options: { id: string; name: string; color?: string }[]
</script>

<select {value} on:change>
    {#each options as option (option.id)}
        <option value={option.id} style:color={option.color || 'black'}
            >{option.name}</option
        >
    {/each}
</select>
